/* eslint-disable react/no-unescaped-entities */
import { useContext, useEffect } from 'react'
import { useQuery } from '@tanstack/react-query'
import { useNavigate } from 'react-router-dom'
import styles from './productsList.module.css'
import { ContextApp } from '../../../contexts/ContextApp'
import { ProductsListItem } from '../../ProductsListItem/ProductsListItem'
import { withQuery } from '../../HOCs/withQuery'

function ProductsListInner({ products }) {
  console.log('  ProductsListInner')
  if (!products.products.length) return <p>List is empty ...</p>

  return (
    <div className={styles.wr}>
      {products.products.map((el) => (
        <ProductsListItem
          key={el._id}
          id={el._id}
          name={el.name}
          price={el.price}
          wight={el.wight}
          img={el.pictures}
        />
      ))}
    </div>
  )
}

const ProductListInnerWithQuery = withQuery(ProductsListInner)

export function ProductsList() {
  console.log('  ProductsList')
  const navigate = useNavigate()

  const { getTokenFromLS } = useContext(ContextApp)

  const tokenFromLS = getTokenFromLS()

  // useEffect(() => {
  //   if (!dataSignUpFromLS.length) {
  //     navigate('/signin')
  //   }
  // }, [])

  if (!tokenFromLS.length) {
    useEffect(() => navigate('/signin'))
    return null
  }

  const {
    data: products, isLoading, isError, error, refetch,
  } = useQuery({
    queryKey: ['productsListFetch'],
    queryFn: () => fetch('https://api.react-learning.ru/products', {
      headers: {
        authorization: `Bearer ${tokenFromLS}`,
      },
    }).then((res) => {
      if (res.status >= 400 && res.status < 500) {
        throw new Error(`     .
          . Status: ${res.status}`)
      }

      if (res.status >= 500) {
        throw new Error(`     .
             . Status: ${res.status}`)
      }

      return res.json()
    }),
    enabled: tokenFromLS !== undefined,
  })

  console.log({
    products, isLoading, isError, error, refetch,
  })

  return (
    <ProductListInnerWithQuery
      products={products}
      isLoading={isLoading}
      isError={isError}
      error={error}
      refetch={refetch}
    />
  )
}
