import * as Yup from 'yup'

export const signUpFormValidationSchema = Yup.object({
  email: Yup.string()
    .email('  ')
    .required(' '),
  group: Yup.string()
    .max(10, '     10 ')
    .required(' '),
  password: Yup.string()
    .required('  ')
    .min(5, '   -      5 ')
    .matches(/[a-zA-Z]/, '     '),
})

export const signInFormValidationSchema = Yup.object({
  email: Yup.string()
    .email('  ')
    .required(' '),
  password: Yup.string()
    .required('  ')
    .min(5, '   -      5 ')
    .matches(/[a-zA-Z]/, '     '),
})
